import React from "react";
import { useMappedImages } from "../../hooks/images";
import { graphql, useStaticQuery } from "gatsby";
import Img from "gatsby-image";

export default function Icons({ onGalleryImageClick }) {
  const data = useStaticQuery(graphql`
    query {
      allFile(
        filter: {
          relativePath: {
            in: [
              "projects/team-portals/icons/ae_icons_developer.png"
              "projects/team-portals/icons/ae_icons_partner.png"
              "projects/team-portals/icons/ae_icons_projectmanager.png"
              "projects/team-portals/icons/ae_icons_qa.png"
              "projects/team-portals/icons/ae_icons_stakeholder.png"
              "projects/team-portals/icons/ae_icons_support.png"
              "projects/team-portals/icons/ae_icons_technicallead.png"
              "projects/team-portals/icons/ae_icons_thirdparty.png"
              "projects/team-portals/icons/ae_icons_transition.png"
              "projects/team-portals/icons/ae_icons_analyst.png"
              "projects/team-portals/icons/ae_icons_architect.png"
            ]
          }
        }
      ) {
        ...FluidImagesFragment
      }
    }
  `);
  const optimizedImages = useMappedImages(data.allFile);

  return (
    <>
      <div className="overflow-hidden">
        <div className="flex flex-wrap -mx-2 mb-8">
          {[
            "projects/team-portals/icons/ae_icons_developer.png",
            "projects/team-portals/icons/ae_icons_partner.png",
            "projects/team-portals/icons/ae_icons_projectmanager.png",
            "projects/team-portals/icons/ae_icons_qa.png",
            "projects/team-portals/icons/ae_icons_stakeholder.png",
            "projects/team-portals/icons/ae_icons_support.png",
            "projects/team-portals/icons/ae_icons_technicallead.png",
            "projects/team-portals/icons/ae_icons_thirdparty.png",
            "projects/team-portals/icons/ae_icons_transition.png",
            "projects/team-portals/icons/ae_icons_analyst.png",
            "projects/team-portals/icons/ae_icons_architect.png",
          ].map((value) => (
            <div
              className="w-1/4 sm:w-1/5 sm:w-1/6 md:w-1/10 lg:w-1/12 px-2 py-2 "
              onClick={() => {
                onGalleryImageClick(value);
              }}
            >
              <Img
                className={"cursor-zoom-in"}
                fluid={optimizedImages[value].childImageSharp.fluid}
              />
            </div>
          ))}
        </div>
      </div>
    </>
  );
}
